<template>
    <div>
        <v-menu bottom left>
            <template v-slot:activator="{ on, attrs }">
                <v-btn icon v-bind="attrs" v-on="on">
                    <v-icon color="#273746">fact_check </v-icon>
                </v-btn>
            </template>

            <v-list>
                <v-list-item>
                    <v-list-item @click="deleteDialog = true"> Borrar </v-list-item>
                </v-list-item>
                <v-list-item>
                    <v-list-item @click="swapDialog = true"> Intercambiar</v-list-item>
                </v-list-item>
                <v-list-item>
                    <v-list-item @click="addBelow"> Agregar Abajo</v-list-item>
                </v-list-item>
            </v-list>
        </v-menu>


        <!-- dialog delete -->
        <v-dialog v-model="deleteDialog" persistent max-width="400">
            <v-card>
                <v-card-title class="text-h5"> Borrar </v-card-title>
                <v-card-text>
                    ¿Seguro que desea borrar la referencia <strong>{{ item.reference }}</strong>?, <br /> Se ajustará el
                    contador
                    automaticamente.</v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="#273746" text @click="cancel"> Cancelar </v-btn>
                    <v-btn outlined color="#273746" @click="deleteAction">
                        Si, Borrar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>



        <!-- dialog swapDialog -->
        <v-dialog v-model="swapDialog" persistent max-width="400">
            <v-card>
                <v-card-title class="text-h5">Intercambiar </v-card-title>
                <v-card-text>
                    ¿Desea intercambiar la referencia <strong>{{ item.reference }}</strong> con ?, <br />

                    <br />
                    <v-select outlined label="Referencias" :items="filterItems" required item-text="reference"
                        v-model="newReference" return-object></v-select>

                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="#273746" text @click="cancel"> Cancelar </v-btn>
                    <v-btn outlined color="#273746" :disabled="!newReference" @click="swapAction">
                        Si, Intercambiar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

    </div>
</template>
<script>
import { mapState, mapActions } from "vuex";

export default {
    name: 'BtnActionSection',
    props: {
        item: {
            type: Object,
            default: null
        },
        nameEdit: {
            type: String,
            default: null,
        }
    },
    data() {
        return {
            deleteDialog: false,
            swapDialog: false,
            newReference: null

        }
    },
    computed: {
        ...mapState({
            data: (state) => state.customers.findCustomer,
        }),
        filterItems() {
            const data = this.data[this.nameEdit].data;
            const id = this.item.reference;
            const filter = data.filter((item) => item.reference != id);
            return filter;
        },
    },
    methods: {
        ...mapActions({
            getItem: "customers/search",
            saveItem: "customers/save",
        }),
        save() {
            this.saveItem(this.data)
            this.deleteDialog = false;
            this.swapDialog = false;
            this.newReference = null;
        },
        cancel() {
            this.deleteDialog = false;
            this.swapDialog = false;
            this.newReference = null;
        },
        deleteAction() {
            const data = this.data[this.nameEdit].data;
            const id = this.item.reference;

            const filter = data.filter((item) => item.reference != id);
            if (filter.length === 0) {
                this.data[this.nameEdit].data = filter;
                this.save();
                return 0;
            }

            let count = 0;
            const orderFilter = filter.map(item => {
                count++;
                const newItem = JSON.parse(JSON.stringify(item));
                newItem.reference = count;
                return newItem;
            })
            this.data[this.nameEdit].data = orderFilter;
            this.save();

        },
        swapAction() {
            const data = this.data[this.nameEdit].data;
            const one = JSON.parse(JSON.stringify(this.item));
            const two = JSON.parse(JSON.stringify(this.newReference));
            one.reference = this.newReference.reference;
            two.reference = this.item.reference;
            data[one.reference - 1] = one;
            data[two.reference - 1] = two;
            const newData = JSON.parse(JSON.stringify(data));
            this.data[this.nameEdit].data = newData;
            this.save();
        },
        addBelow() {
            const position = this.item.reference;
            this.$emit('newItem');
            const data = this.data[this.nameEdit].data;
            const itemNew = data.pop();
            itemNew.result = JSON.parse(JSON.stringify(this.data[this.nameEdit].config))
            data.splice(position, 0, itemNew);
            let count = 0;
            const order = data.map(item => {
                count++;
                const newItem = JSON.parse(JSON.stringify(item));
                newItem.reference = count;
                return newItem;
            })
            const newData = JSON.parse(JSON.stringify(order));
            this.data[this.nameEdit].data = newData;
            this.save();
        }
    }
}

</script>